export default {
	data() {
		return {
			scrollHeight:'500px',
			leftArray:[],
			mainArray:[],
			topArr:[],
			leftIndex:0,
			scrollInto:''
		}
	},
	onLoad(){
		uni.getSystemInfo({
			success:(res)=>{
				/* 设置当前滚动容器的高，若非窗口的高度，请自行修改 */
				this.scrollHeight=`${res.windowHeight}px`;
			}
		});
	},
	mounted(){
		this.getListData();
	},
	methods: {
		/* 获取列表数据 */
		getListData(){
			// Promise 为 ES6 新增的API ，有疑问的请自行学习该方法的使用。
			new Promise((resolve,reject)=>{
				/* 因无真实数据，当前方法模拟数据。正式项目中将此处替换为 数据请求即可 */
				uni.showLoading();
				setTimeout(()=>{
					let [left,main]=[[],[]];
					
					for(let i=0;i<10;i++){
						left.push(`${i+1}类商品`);
						
						let list=[];
						for(let j=0;j<(i+1);j++){
							list.push(j);
						}
						main.push({
							title:`第${i+1}类商品标题`,
							list
						})
					}
					
					// 将请求接口返回的数据传递给 Promise 对象的 then 函数。
					resolve({left,main});
				},1000);
			}).then((res)=>{
				console.log('-----------请求接口返回数据示例-------------');
				console.log(res);
				
				uni.hideLoading();
				this.leftArray=res.left;
				this.mainArray=res.main;
				
				// DOM 挂载后 再调用 getElementTop 获取高度的方法。
				this.$nextTick(()=>{
					this.getElementTop();
				});
			});
		},
		/* 获取元素顶部信息 */
		getElementTop(){
			/* Promise 对象数组 */
			let p_arr=[];
			
			/* 新建 Promise 方法 */
			let new_p=(selector)=>{
				return new Promise((resolve,reject)=>{
					let view = uni.createSelectorQuery().select(selector);
					view.boundingClientRect(data => {
						resolve(data.top);
					}).exec();
				})
			}
			
			/* 遍历数据，创建相应的 Promise 数组数据 */
			this.mainArray.forEach((item,index)=>{
				p_arr.push(new_p(`#item-${index}`));
			});
			
			/* 所有节点信息返回后调用该方法 */
			Promise.all(p_arr).then((data)=>{
				this.topArr=data;					
			});
		},
		/* 主区域滚动监听 */
		mainScroll(e){
			let top =e.detail.scrollTop;
			let index=0;
			/* 查找当前滚动距离 */
			for(let i = (this.topArr.length-1);i>=0;i--){
				/* 在部分安卓设备上，因手机逻辑分辨率与rpx单位计算不是整数，滚动距离与有误差，增加2px来完善该问题 */
				if((top+2)>=this.topArr[i]){
					index = i;
					break;
				}
			}
			this.leftIndex=(index < 0 ? 0: index);
		},
		/* 左侧导航点击 */
		leftTap(e){
			let index=e.currentTarget.dataset.index;
			this.scrollInto=`item-${index}`;
		}
	}
}
